.timer {
  flex: 1 1 auto;
  border-radius: 15px;
  box-shadow: 0 0 1px 2px rgba(102, 51, 153, 0.4);
  padding: 40px 0;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.clock {
  height: 200px;
  width: 200px;
  box-shadow: 0 0 10px 10px rgba(255, 99, 71, 0.9),
    0px 0px 5px 5px inset rgba(255, 99, 71, 0.5);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  font-weight: bold;
  margin: 15px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .clock {
    height: 180px;
    width: 180px;
    margin: 20px 40px 30px 40px;
    font-size: 40px;
  }
  .timer {
    width: 100%;
    margin: 0;
    box-shadow: none;
    padding: 20px 0;
  }
}
