@import url(https://fonts.googleapis.com/css?family=Orbitron|Roboto|Text+Me+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100vw;
    color: #ff6347;
}

.header h1 {
    text-align: center;
    font-size: 25px;
}
.quote {
    flex: 1 1 auto;
    width: 25%;
    color: #2c3d55;
    box-shadow: 0 0 1px 1px rgb(160, 227, 219);
    border-radius: 15px;
    text-align: center;
    margin: 10px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    font-family: "Text Me One", sans-serif;
}

.quote p {
    padding: 0 20px;
    font-size: 23px;
}

.quote p:before,
.quote p:after {
    content: "\201C";
    color: rgb(255, 99, 71);
    padding: 0 10px;
    font-size: 50px;
    font-weight: bold;
    font-family: auto;
    display: block;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
}

.quote p:after {
    content: "\201D";
    text-align: right;
}

@media (min-width: 320px) and (max-width: 480px) {
    .quote {
        width: 100%;
        margin: 0 10px;
    }
    .quote p {
        padding: 0;
        font-size: 18px;
        margin: 0;
    }
    .quote p:before,
    .quote p:after {
        font-size: 25px;
        padding: 0 5px;
        margin: 10px 0 0 0;
    }
}
.btn {
    padding: 10px 0;
    min-width: 90px;
    margin: 10px;
    outline: none;
    border-radius: 8px;
    border: 1px solid transparent;
    cursor: pointer;
    -webkit-transition: all 2ms ease;
    transition: all 2ms ease;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    background-color: #e5e5e5;
}

.btn-start {
    box-shadow: 0 0 1px 1px #6dc9dd;
}

.btn-start:hover {
    box-shadow: 0 0 2px 2px #6dc9dd;
}

.btn-start:active {
    background: -webkit-linear-gradient(#f4f3ea, #6dc9dd);
    background: linear-gradient(#f4f3ea, #6dc9dd);
}

.btn-stop {
    box-shadow: 0 0 1px 1px #d36582;
}

.btn-stop:hover {
    box-shadow: 0 0 2px 2px #d35682;
}

.btn-stop:active {
    background: -webkit-linear-gradient(#f4f3ea, #d36582);
    background: linear-gradient(#f4f3ea, #d36582);
}

.btn-reset {
    box-shadow: 0 0 1px 1px #b8cdf8;
}

.btn-reset:hover {
    box-shadow: 0 0 2px 2px #b8cdf8;
}

.btn-reset:active {
    background: -webkit-linear-gradient(#f4f3ea, #becdf8);
    background: linear-gradient(#f4f3ea, #becdf8);
}
.timer {
  flex: 1 1 auto;
  border-radius: 15px;
  box-shadow: 0 0 1px 2px rgba(102, 51, 153, 0.4);
  padding: 40px 0;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.clock {
  height: 200px;
  width: 200px;
  box-shadow: 0 0 10px 10px rgba(255, 99, 71, 0.9),
    0px 0px 5px 5px inset rgba(255, 99, 71, 0.5);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  font-weight: bold;
  margin: 15px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .clock {
    height: 180px;
    width: 180px;
    margin: 20px 40px 30px 40px;
    font-size: 40px;
  }
  .timer {
    width: 100%;
    margin: 0;
    box-shadow: none;
    padding: 20px 0;
  }
}

.timer-settings {
  flex: 1 1 25%;
  box-shadow: 0 0 1px 1px rgb(255, 255, 0);
  padding: 40px 0;
  border-radius: 15px;
  margin: 10px;
}

.timer-settings h2 {
  text-align: center;
  margin: 0 0 40px 0;
}

.timer-settings label {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
}

.timer-settings input[type="number"] {
  width: 40px;
  padding: 5px;
  margin: 5px;
  box-shadow: 0 0 1px 1px rgb(255, 99, 71);
  border-radius: 3px;
  border: 0;
  font-size: 16px;
}

.timer-settings input[type="number"]:focus {
  box-shadow: 0 0 2px 2px rgb(255, 99, 71);
  outline: 0;
}

.timer-settings input[type="checkbox"] {
  display: none;
}

.timer-settings span {
  width: 25px;
  margin: 5px;
  box-shadow: 0 0 1px 1px rgb(255, 99, 71);
  border-radius: 3px;
  height: 25px;
  cursor: pointer;
  position: relative;
  background-color: #ffffff;
}

.timer-settings span:hover {
  box-shadow: 0 0 2px 2px rgb(255, 99, 71);
}

.timer-settings input[type="checkbox"]:checked + span::before {
  content: "";
  position: absolute;
  border-left: 3px solid #2c3d55;
  border-bottom: 3px solid #2c3d55;
  width: 15px;
  height: 5px;
  top: 6px;
  left: 4px;
  -webkit-transform: rotate(80deg);
  transform: rotate(315deg);
}

@media (min-width: 320px) and (max-width: 480px) {
  .timer-settings {
    width: 100%;
    padding: 15px 0;
    box-shadow: none;
  }
  .timer-settings h2 {
    text-align: left;
    margin-left: 20px;
    font-size: 18px;
  }
}

body {
    padding: 0;
    background: #f4f3ea;
    color: #41463d;
    font-family: "Roboto", sans-serif;
}

.container {
    width: 100vw;
    display: flex;
    flex-flow: row wrap;
}

@media (min-width: 320px) and (max-width: 480px) {
    body {
        background-color: #ffffff;
    }
}
